// Base
.footer
{
    padding: 1.5rem 0;

    .clearfix();
    > .footer-introduce
    {
        margin-bottom: .2rem;
    }
    &.footer--simple
    {
        text-align: center;
        > .footer-introduce
        {
            overflow: hidden;

            white-space: nowrap;
            text-overflow: ellipsis;
        }
    }
}
