// Base
.layout
{
    min-height: 100%;
    > .layout-header
    {
        z-index: @z-index-layout-header;

        width: 100%;
        height: (@layout-header-height + .125rem);

        border-top: 1px solid @color-gray-lighter;
        border-bottom: 1px solid @color-gray-lighter;
        background: #fff;

        .clearfix();
    }
    > .layout-necker
    {
        border-bottom: 1px solid @color-gray-lighter;
        background: @background-necker;
    }
    > .layout-mesoer
    {
        padding: 0;
    }
    > .layout-footer
    {
        border-top: 1px solid @color-gray-lighter;
        border-bottom: 1px solid @color-gray-lighter;
        background: @background-necker;
    }
    /* 天才布局 */
    &.layout--genius
    {
        > .layout-header
        {
            position: fixed;
            top: auto;
            bottom: 0;

            background: @background-html;
        }
        > .layout-necker
        {
            border-top: 1px solid @color-gray-lighter;
        }
        > .layout-mesoer
        {
            padding-bottom: @layout-header-height;
        }
        > .layout-footer
        {
            padding-bottom: @layout-header-height;

            border-bottom: 1px solid @color-gray-lighter;
        }
        > .layout-mesoer + .layout-footer
        {
            margin-top: -@layout-header-height;
        }
        @media (min-width: @screen-medium-min)
        {
            > .layout-header
            {
                top: 0;
                bottom: auto;
            }
            > .layout-mesoer
            {
                padding-top: @layout-header-height;
                padding-bottom: 0;
            }
            > .layout-footer
            {
                padding-bottom: 0;
            }
            > .layout-header + .layout-necker
            {
                padding-top: @layout-header-height;

                border-top: none;
            }
            > .layout-necker + .layout-mesoer
            {
                padding-top: 0;
            }
            > .layout-mesoer + .layout-footer
            {
                margin-top: 0;
            }
        }
    }
}

