.group-button(@class-button; @border-radius){
  position: relative;
  display: inline-block;
  vertical-align: middle;
  > .@{class-button} {
    position: relative;
    border-radius: 0;
    float: left;
    margin-left: -1px;
    &:hover,&:focus,&:active,&.selected {
      z-index: 2;
    }
    &:first-child {
      .border-left-radius(@border-radius);
      margin-left: 0;
    }
    &:last-child {
      .border-right-radius(@border-radius);
    }
  }
}




