// Base
.group{
    .clearfix();
    // group--button
    &.group--button {
        margin-right: 0.3125rem;
        &:last-child {
            margin-right: 0;
        }
        .clearfix();
        > .button {
            margin-right: 0;
        }
        .group-button(button;@border-radius-base);
        .group-button(button--small;@border-radius-small);
        .group-button(button--medium;@border-radius-medium);
        .group-button(button--large;@border-radius-large);
    }

    &.group--note {
    	.note{
    		margin-bottom: 0;
    		border-bottom: none;
    		&:last-child {
    			margin-bottom: @spacing-medium;
    			border: 1px solid @color-gray-lighter;
    		}
    	}
    }
}