.button-size(@padding; @font-size; @border-radius) {
  padding: @padding;
  font-size: @font-size;
  border-radius: @border-radius;
}

.button-color(@text-color; @background-color; @border-color) {
  color: @text-color;
  background-color: @background-color;
  border-color: @border-color;
}

// Generate
.button-generate(@text-color; @background-color; @border-color) {
  .button-color(@text-color; @background-color; @border-color);
  &:hover {
    .button-color(tint(@text-color, 15%); tint(@background-color, 15%); tint(@border-color, 15%));
  }
  &:active,
  &.selected {
    .button-color(shade(@text-color, 5%); shade(@background-color, 5%); shade(@border-color, 5%));
  }
}

// Kind Hollow
.button-hollow(@border-color){
  background: none;
  border-color: @border-color;
  color: @border-color;
  &:hover {
    background: lighten(@border-color, 38%);
  }
}

// Kind Link
.button-link(@text-color) {
  background: none;
  color: @text-color;
  border-color: transparent;
  &:hover,
  &:focus,
  &:active {
    color: @text-color;
    text-decoration: underline;
    background: none;
  }

  &.selected {
    color: @text-color;
  }
}



