// Base
.necker {

    > .necker-button {
        height: 2rem;
        padding: 5px 0;
        text-align: right;
        background: transparent;
    }

    > .necker-content {
        padding: 2rem 0;
    }

    &.necker--simple {
        > .necker-content {
            text-align: center;
            > h1,h2,h3,h4,h5,h6 {
                margin: 0;
            }
        }
    }

    > .necker-button + .necker-content {
        margin-top: -2rem;
    }
}