// Base
.header
{
    position: relative;

    margin-right: (@grid-item-gutter / -2);
    margin-left: (@grid-item-gutter / -2);

    .clearfix();
    @media (min-width: @screen-medium-min)
    {
        margin-right: 0;
        margin-left: 0;
    }
    > .header-switch
    {
        display: none;
    }
    > .header-surplus
    {
        display: none;
    }
    > .header-navbar
    {
        min-height: @layout-header-height;
        padding: 0 ;

        background: transparent;

        .clearfix();
    }
    &.header--advance
    {
        > .header-switch
        {
            position: absolute;
            top: 0;
            left: 0;

            display: block;

            border-right: 1px solid @color-gray-lighter;
            > .header-switch-title
            {
                line-height: @layout-header-height;

                overflow: hidden;

                width: @layout-header-height;
                height: @layout-header-height;
                padding: 0 (@grid-item-gutter / 2);
                > a,
                span
                {
                    display: block;
                    > img
                    {
                        display: block;

                        width: 100%;
                        padding: 10px 0;
                    }
                }
            }
            @media (min-width: @screen-medium-min)
            {
                border-left: 1px solid @color-gray-lighter;
            }
        }
        > .header-surplus
        {
            position: absolute;
            top: 0;
            right: 0;

            display: block;

            border-left: 1px solid @color-gray-lighter;
            > .header-surplus-title
            {
                font-size: 2rem;
                line-height: @layout-header-height;

                width: @layout-header-height;
                height: @layout-header-height;
                padding: 0 (@grid-item-gutter / 2);

                text-align: center;

                color: @color-gray-light;
            }
            > .header-surplus-list
            {
                position: absolute;
                z-index: (@z-index-layout-header + 1);
                top: @layout-header-height;
                right: 0;

                display: none;
                overflow: hidden;

                width: 120px;
                padding: .4rem 0;

                border: 1px solid @color-gray-lighter;
                background: @background-html;
                @media (min-width: @screen-tiny-min)
                {
                    width: 160px;
                }

                > ul,ol {
                    > li {
                        display: list-item;
                        padding: 0.125rem 0.625rem;
                        &:hover {
                            background-color: @color-gray-lighter;
                        }
                        &.active {
                            background-color: darken(@color-gray-lighter,10%);
                        }
                    }
                }
            }
            &:hover
            {
                cursor: pointer;
                > .header-surplus-title
                {
                    color: @color-gray;
                }
                > .header-surplus-list
                {
                    display: inherit;
                }
            }
            @media (min-width: @screen-medium-min)
            {
                border-right: 1px solid @color-gray-lighter;
            }
        }
        > .header-navbar
        {
            padding: 0 @layout-header-height;
        }
    }
}

// Genius Mode
.layout--genius
{
    .header
    {
        &.header--advance
        {
            > .header-surplus
            {
                > .header-surplus-list
                {
                    top: auto;
                    bottom: @layout-header-height;
                    @media (min-width: @screen-medium-min)
                    {
                        top: @layout-header-height;
                        bottom: auto;
                    }
                }
            }
        }
    }
}
