.grid
{
    height: auto;
    &.grid--flow
    {
        position: relative;

        display: block;

        .clearfix();
        &.grid--flow--gutter
        {
            margin-right: (@grid-item-gutter / -2);
            margin-left: (@grid-item-gutter / -2);
        }
        > .grid-item
        {
            display: block;
            float: left;
        }
    }
}

.make-grid-item-gutter();

.make-grid-items(bitty);

@media (min-width: @screen-tiny-min)
{
    .make-grid-items(tiny);
}

@media (min-width: @screen-small-min)
{
    .make-grid-items(small);
}

@media (min-width: @screen-medium-min)
{
    .make-grid-items(medium);
}

@media (min-width: @screen-large-min)
{
    .make-grid-items(large);
}

@media (min-width: @screen-huge-min)
{
    .make-grid-items(huge);
}
