// Base
.navbar
{
    position: relative;

    width: 100%;

    .clearfix();
    > .navbar-brand
    {
        display: block;
        float: left;
    }
    > .navbar-nav
    {
        position: relative;

        display: block;
        float: right;

        border-left: 1px solid @color-gray-lighter;
        > .navbar-nav-title
        {
            line-height: @layout-header-height;

            height: @layout-header-height;
            padding: 0 (@grid-item-gutter / 2);

            text-align: center;

            color: @color-gray-light;
        }
        > .navbar-nav-list
        {
            position: absolute;
            z-index: (@z-index-layout-header + 1);
            top: @layout-header-height;
            right: 0;

            display: none;
            overflow: hidden;

            width: 120px;
            padding: .4rem 0;

            border: 1px solid @color-gray-lighter;
            background: @background-html;
            @media (min-width: @screen-tiny-min){
                width: 160px;
            }
            > li {
                display: list-item;
                padding: 0.125rem 0.625rem;
                &:hover {
                    background-color: @color-gray-lighter;
                }
                &.active {
                    background-color: darken(@color-gray-lighter,10%);
                }
            }
        }
        &:hover
        {
            cursor: pointer;
            > .navbar-nav-title
            {
                color: @color-gray;
            }
            > .navbar-nav-list
            {
                display: inherit;
            }
        }
    }
    @media (min-width: @screen-medium-min)
    {
        > .navbar-nav
        {
            display: block;
            float: left;

            border: none;
            > .navbar-nav-title
            {
                display: none;
            }
            > .navbar-nav-list
            {
                position: relative;
                top: auto;
                bottom: auto;
                left: auto;

                display: block;
                overflow: hidden;

                width: auto;
                height: @layout-header-height;
                margin-left: auto;
                padding: 0;

                border: none;
                background: transparent;

                .clearfix();
                > li
                {
                    line-height: @layout-header-height;

                    display: block;
                    float: left;

                    height: @layout-header-height;
                    padding: 0 .625rem;
                    background: none;
                    > a,
                    span
                    {

                        color: @color-gray;
                    }
                    &:hover {
                        background: none;
                        > a ,span {
                            color: @color-minor;
                        }
                    }
                    &.active
                    {
                        background: none;
                        border-bottom: 1px solid @color-primary;
                        > a,
                        span
                        {
                            color: @color-primary;
                        }
                    }
                }
            }
            &.pull-left
            {
                float: left;
            }
            &.pull-right
            {
                float: right;
            }
        }
    }
}

// header--advance
.header
{
    &.header--advance
    {
        > .header-navbar
        {
            > .navbar
            {
                > .navbar-brand
                {
                    display: none;
                }
                > .navbar-nav
                {
                    display: table-cell;
                    float: none;

                    width: 1%;
                    > .navbar-nav-list
                    {
                        left: 50%;

                        width: 120px;
                        margin-left: -60px;
                        @media (min-width: @screen-tiny-min)
                        {
                            width: 160px;
                            margin-left: -80px;
                        }
                    }
                }
                @media (min-width: @screen-medium-min)
                {
                    > .navbar-nav
                    {
                        display: block;
                        float: left;

                        width: auto;
                        > .navbar-nav-title
                        {
                            display: none;
                        }
                        > .navbar-nav-list
                        {
                            top: auto;
                            bottom: auto;
                            left: auto;

                            width: auto;
                            margin-left: auto;
                            padding: 0;
                        }
                        &.pull-left
                        {
                            float: left;
                        }
                        &.pull-right
                        {
                            float: right;
                        }
                        &:first-child
                        {
                            padding-left: .625rem;
                        }
                        &:last-child
                        {
                            padding-right: .625rem;
                        }
                    }
                }
            }
        }
    }
}

// layout--genius header--advance
.layout--genius
{
    .header
    {
        > .header-navbar
        {
            > .navbar
            {
                > .navbar-nav
                {
                    > .navbar-nav-list
                    {
                        top: auto;
                        bottom: @layout-header-height;
                        @media (min-width: @screen-medium-min)
                        {
                            bottom: auto;
                        }
                    }
                }
            }
        }
    }
}
