/* 清除浮动和流动布局 */
.clearfix
{
    .clearfix();
}

.pull
{
    .clearfix();
}

.pull-left
{
    float: left;
}

.pull-right
{
    float: right;
}

/* 显示隐藏与可见行 */
.hide
{
    display: none !important;
}

.show
{
    display: block !important;
}

.invisible
{
    visibility: hidden;
}


/* 强制文本换行 */
.textwrap,
.textwrap td,
.textwrap th
{
    word-wrap: break-word;
    word-break: break-all;
}

.textwrap--table
{
    table-layout: fixed;
}
