// Base
.banner {
    width: 100%;
    position: relative;
    padding: 2rem 0;
    > .banner-title {
        margin-top: 0;
    }
    @media (min-width: @screen-small-min){
        padding: 4.5rem 0;
    }
    @media (min-width: @screen-medium-min){
        padding: 8rem 0;
    }

    &.banner--simple {
        text-align: center;
    }
}

.warpper--darker {
    .banner {
        > .banner-title {
            color: @color-gray-lighter;
        }
        a {
            color: @color-gray-lighter;
        }
    }
}