// Base
.card {
	position: relative;
    border: 1px solid @color-gray-lighter;
    border-radius: @border-radius-base;
    margin-top: 0.625rem;
    margin-bottom: 0.625rem;

    > .card-head {
    	position: relative;
    	border-bottom: 1px solid @color-gray-lighter;
    	.clearfix();
    	> .card-head-title {
    		margin:0;
    		font-size: @font-size-h5;
    		padding: 0.3125rem 0.625rem;
    	}
    	> .card-head-label {
    		position: absolute;
    		right: 0.3125rem;
    		top: -0.5rem;
    		height: 1rem;
    		overflow: hidden;
    		font-size: 0.625rem;
    		> a ,span{
    			font-size: 0.625rem;
    		}
    	}
    }

    > .card-neck {
		padding: 0.625rem;
		height: 5.75rem;
		overflow: hidden;
		> * {
			margin: 0;
			height: 4.5rem;
			overflow: hidden;
			margin-bottom:0.625rem;
		}
    }

    > .card-foot {
    	padding: 0.3125rem 0.625rem;
    	border-top: 1px solid @color-gray-lighter;
        background-color: @background-necker;
    }
}