// Base
.abort {
    max-width: 810px;
    padding:  2rem 0;
    margin: 0 auto;
    > .abort-title {
        font-weight: bold;
    }
    > .abort-desc {
        margin-bottom: 1rem;
    }
    &.abort--simple {
        > .abort-title {
            text-align: center;
            font-size: 6.25rem;
        }
        > .abort-desc {
            display: none;
        }
    }
}