/***********************************
 * Docs
 *
 * kind: hollow / link
 * size: bitty / tiny / small / medium / large / huge
 * skin: default / primary / success
 *       warning / error / minor
 ***********************************/

@button-disable-color               : #ccc;
@button-disable-background          : #f3f5f7;
@button-disable-border	            : @color-gray-lighter;

// default
@button-default-text-color          : @color-gray-dark;
@button-default-background-color    : @color-default;
@button-default-border-color	    : @color-gray-light;

@button-primary-text-color          : #fff;
@button-primary-background-color    : lighten(@color-primary,20%);
@button-primary-border-color	    : shade(@button-primary-background-color, 5%);

@button-success-text-color          : #fff;
@button-success-background-color    : @color-success;
@button-success-border-color	    : shade(@button-success-background-color, 5%);

@button-warning-text-color          : #fff;
@button-warning-background-color    : @color-warning;
@button-warning-border-color	    : shade(@button-warning-background-color, 5%);

@button-error-text-color            : #fff;
@button-error-background-color      : @color-error;
@button-error-border-color          : shade(@button-error-background-color, 5%);

@button-minor-text-color            : #fff;
@button-minor-background-color      : @color-minor;
@button-minor-border-color	        : shade(@button-minor-background-color, 5%);

// Base
.button
{
    font-weight: 100;
    line-height: @line-height-base;

    position: relative;

    display: inline-block;

    margin-right: .3125rem;
    padding-right: .3125rem;
    padding-left: .3125rem;

    transition: box-shadow .2s cubic-bezier(.4, 0, 1, 1),
    background-color .2s cubic-bezier(.4, 0, .2, 1),
    color .2s cubic-bezier(.4, 0, .2, 1);
    text-align: center;
    vertical-align: middle;
    white-space: nowrap;

    color: @color-gray;
    border: 1px solid @color-gray-light;
    border-radius: @border-radius-base;
    outline: none;

    touch-action: manipulation;
    -webkit-appearance: none;
    background: none;
    > *
    {
        pointer-events: none;
    }
    &:hover
    {
        color: tint(@color-gray, 15%);
        border-color: tint(@color-gray-light, 15%);
        background-color: tint(@color-gray-lighter, 15%);
    }
    &:active,
    &.selected
    {
        color: shade(@color-gray, 5%);
        border-color: shade(@color-gray-light, 5%);
        background-color: shade(@color-gray-lighter, 5%);
        background-image: none;
    }
    &:last-child
    {
        margin-right: 0;
    }
    &.disabled,
    &[disabled],
    fieldset[disabled] &
    {
        &,
        &:hover,
        &:active,
        &.selected
        {
            .button-color(@button-disable-color; @button-disable-background; @button-disable-border);
        }
    }
    &.button--block
    {
        display: block;

        width: 100%;
        margin-bottom: .3125rem;
        &:last-child
        {
            margin-bottom: 0;
        }
    }
    // size
    &.button--small
    {
        .button-size(@padding-small; @font-size-small; @border-radius-small);
    }
    &.button--medium
    {
        .button-size(@padding-medium; @font-size-base; @border-radius-medium);
    }
    &.button--large
    {
        .button-size(@padding-large; @font-size-large; @border-radius-large);
    }
    // // generate
    &.button--default
    {
        .button-generate(@button-default-text-color; @button-default-background-color; @button-default-border-color);
        &.button--hollow
        {
            .button-hollow(@button-default-border-color);
        }
        &.button--link
        {
            .button-link(@color-gray);
        }
    }
    &.button--primary
    {
        .button-generate(@button-primary-text-color; @button-primary-background-color; @button-primary-border-color);
        &.button--hollow
        {
            .button-hollow(@button-primary-border-color);
        }
        &.button--link
        {
            .button-link(@color-primary);
        }
    }
    &.button--success
    {
        .button-generate(@button-success-text-color; @button-success-background-color; @button-success-border-color);
        &.button--hollow
        {
            .button-hollow(@button-success-border-color);
        }
        &.button--link
        {
            .button-link(@color-success);
        }
    }
    &.button--warning
    {
        .button-generate(@button-warning-text-color; @button-warning-background-color; @button-warning-border-color);
        &.button--hollow
        {
            .button-hollow(@button-warning-border-color);
        }
        &.button--link
        {
            .button-link(@color-warning);
        }
    }
    &.button--error
    {
        .button-generate(@button-error-text-color; @button-error-background-color; @button-error-border-color);
        &.button--hollow
        {
            .button-hollow(@button-error-border-color);
        }
        &.button--link
        {
            .button-link(@color-error);
        }
    }
    &.button--minor
    {
        .button-generate(@button-minor-text-color; @button-minor-background-color; @button-minor-border-color);
        &.button--hollow
        {
            .button-hollow(@button-minor-border-color);
        }
        &.button--link
        {
            .button-link(@color-minor);
        }
    }
}
