// Base
.warpper
{
	background-position: center;
	background-size: 100%;
    border-bottom: 1px solid @color-gray-lighter;
    width: 100%;
    &.warpper--solid
    {
        > .warpper-content
        {
            @media (min-width: @screen-tiny-min)
            {
                width: @warpper-tiny;
            }
            @media (min-width: @screen-small-min)
            {
                width: @warpper-small;
            }
            @media (min-width: @screen-medium-min)
            {
                width: @warpper-medium;
            }
            @media (min-width: @screen-large-min)
            {
                width: @warpper-large;
            }
            @media (min-width: @screen-huge-min)
            {
                width: @warpper-huge;
            }
        }
    }
    > .warpper-content
    {
        position: relative;

        max-width: @warpper-huge;
        margin: 0 auto;
        padding: 0 (@grid-item-gutter / 2);

        .clearfix();
    }
    &:last-child {
        border-bottom: none;
    }
}

// Kind
.warpper {
    &.warpper--lighter{
        color: @color-gray-darker;
        background-color: lighten(@color-gray-lighter, 5%);
        border-bottom: 1px solid @color-gray-lighter;
    }
    &.warpper--darker{
        color: @color-gray-lighter;
        background-color: darken(@color-gray-darker, 5%);
        border-bottom: 1px solid @color-gray-darker;
    }
}
