// Base
.tab {
    > .tab-bar{
        border-bottom: 1px solid #ddd;
        margin:  0 0 10px;
        padding: 0;

        > .tab-bar-item {
            display: inline-block;
            border: 1px solid transparent;
            border-bottom: none;
            bottom:  -1px;
            position: relative;
            padding:  6px 12px;
            cursor:  pointer;

            &[aria-selected=true] {
                background: #fff;
                border-color: #ddd;
                color: black;
            }

            &[aria-disabled=true] {
                color: #eee;
                cursor: default;
            }

            &:focus {
                box-shadow: 0 0 5px hsl(208, 99%, 50%);
                border-color: hsl(208, 99%, 50%);
                outline: none;
                &:after {
                    content:'';
                    position: absolute;
                    height: 5px;
                    left: -4px;
                    right: -4px;
                    bottom: -5px;
                    background: #fff;
                }
            }
        }
    }
}